.homeBody {
	width: 60%;
	height: 1000px;
	background: url(../assets/plaid.jpg);
	background-size: 400px;
	border: solid 5px #674427;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 10px;
	padding: 10px;
}

.rowOne {
	width: 100%;
	height: 400px;
	background-color: #ce7426;
	display: flex;
	flex-direction: row;
	align-items: center;
}

.divider {
	width: 90%;
	height: 0px;
	margin: 5px;
	border-bottom: dashed 10px #59391d;
}